export default{
  name:'header1',
  data () {
    return{
    }
  },
  props: {
    title: {
      type:String,
    },
    operaText: {
      type:String
    }
  },
  methods: {
    register (operaText) {
      if(operaText=='注册'){
        this.$router.push('register');
      }
    },
    back(){
      this.$router.go(-1);
    }
  }
}



